import { Component, OnInit } from '@angular/core';
import { APP_ROUTE } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { SubscriptionTier } from '@core/enums/profile.enum';
import { BusinessProfile } from '@core/models';
import { ConfettiService } from '@core/services/confetti.service';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { SharedModule } from '@shared/shared.module';
import { Observable, filter, take } from 'rxjs';

@Component({
    selector: 'findqo-welcome-business',
    templateUrl: './welcome-business.page.html',
    styleUrl: './welcome-business.page.scss',
    imports: [SharedModule]
})
export class WelcomeBusinessPage implements OnInit {
	constructor(
		private readonly confetti: ConfettiService,
		private readonly auth: AuthService,
		private readonly myProfileFacade: MyProfileFacade,
	) {
		this.businessProfile$ = myProfileFacade.getBusinessProfile$();
	}

	readonly featureList = {
		limited: ['Add your estate agents to assign properties', 'Post 2 standard ads', 'Get unlimited enquiries', 'See previews of seller leads'],
		unlimitedPremium: [
			'Add your estate agents to assign properties',
			'Post 5 featured and unlimited standard ads',
			'Get seller, rental, and commercial leads',
			'Hot Leads + Bonus leads',
			'Includes in-person valuation seller leads',
			'Dedicated Marketing + CRM Integration',
		],
	};

	readonly subTitle = {
		limited: 'Enjoy the benefits of having an agency account:',
		unlimitedPremium: 'Enjoy the benefits of Premium Unlimited',
	};

	readonly appRoute = APP_ROUTE;
	readonly user = this.auth.user();
	readonly subscriptionTier = SubscriptionTier;

	businessProfile$: Observable<BusinessProfile>;

	ngOnInit(): void {
		this.businessProfile$
			.pipe(
				filter((profile) => !!profile),
				take(1),
			)
			.subscribe(() => {
				this.confetti.celebrate();
			});
	}
}
